import React, { useState, useRef } from "react";
import { useDrag } from "@use-gesture/react";
import { animated, useSpring } from "@react-spring/three";
import { useThree } from "@react-three/fiber";
import * as THREE from "three";
export default function Ball({ setIsDragging, floorPlane }) {
  const [pos, setPos] = useState([3, 1, 0]);
  const { size, viewport } = useThree();
  const aspect = size.width / viewport.width;
  let planeIntersectPoint = new THREE.Vector3();
  const dragObjectRef = useRef();
  const [spring, api] = useSpring(() => ({
    position: pos,
    scale: 1,
    rotation: [0, 0, 0],
  }));
  const bind = useDrag(
      ({ active, movement: [x, y], timeStamp, event }) => {
        if (active) {
          event.ray.intersectPlane(floorPlane, planeIntersectPoint);
          setPos([planeIntersectPoint.x, 1, planeIntersectPoint.z]);
        }

        setIsDragging(active);
        api.start({
          position: pos,
        });
        return timeStamp;
      },
      { delay: true }
  );

  return (
      <animated.mesh {...spring} {...bind()} castShadow>
        <sphereGeometry args={[0.3, 64, 32]} ref={dragObjectRef}
                        attach="geometry"/>
        <meshStandardMaterial color="hotpink"  />
        <meshNormalMaterial attach="material" />
      </animated.mesh>
  );
}