import React, {Suspense, useState} from 'react'
import {Canvas} from '@react-three/fiber'
import {OrbitControls} from '@react-three/drei'
import Cat from "./models/Cat";
import {Plane, Vector3} from "three";
import Ball from "./models/Ball";
import BottomNavigation from "@mui/material/BottomNavigation";
import BottomNavigationAction from "@mui/material/BottomNavigationAction";
import AddCircleOutlineIcon from '@mui/icons-material/AddCircleOutline';
import ClearIcon from '@mui/icons-material/Clear';

export default function App() {

  const [isDragging, setIsDragging] = useState(false);
  const floorPlane = new Plane(new Vector3(0, 1, 0), 0);
  const [items, setItems] = useState([])

  const appendBall = () => {
    setItems([1, ...items]);
  }
  const clearBall = () => {
    setItems([])
  }

  return (
      <>
        <div style={{height: "90%"}}>
          <Canvas shadows>
            <Suspense fallback={null}>
              <ambientLight />
              <Cat/>
              {items.map((props, i) => (
                  <Ball setIsDragging={setIsDragging} floorPlane={floorPlane}/>
              ))}
              {/*<Box/>*/}
              <planeHelper args={[floorPlane, 5, "red"]}/>
              <OrbitControls
                  minAzimuthAngle={-Math.PI / 4}
                  maxAzimuthAngle={Math.PI / 4}
                  minPolarAngle={Math.PI / 6}
                  maxPolarAngle={Math.PI - Math.PI / 6}
                  minZoom={10} maxZoom={50} enabled={!isDragging}/>
            </Suspense>
          </Canvas>
        </div>
        <BottomNavigation
            showLabels
            value={1}
        >
          <BottomNavigationAction label="Add balls" icon={<AddCircleOutlineIcon/>} onClick={appendBall}/>
          <BottomNavigationAction label="Clear balls" icon={<ClearIcon/>} onClick={clearBall}/>
        </BottomNavigation>
      </>
  )
}
